.navbar {
    height: 10px;
   
    text-align: center;
    justify-content: center;
    color: white;
    font-family: "Metal Mania";
   

  }
  .wrapper {
    padding: 30px 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
   
    color: black;
    font-family: "Metal Mania";
   
   
  
  }
  .left {
    flex: 2;
    text-align: right;
    font-weight: bold;
    font-family: "Metal Mania";
    color:#777;
    font-size: 20px;
   
   
   
    }
    
    .left a.active{
      color:red
    }
  
  .center {
    flex: 2;
    text-align: center;
    font-weight: bold;
    font-family: "Metal Mania";
    color:#777;
    font-size: 20px;
  }
  .center a.active{
    color:red
  }
  
  .right {
    flex: 2;
    font-weight: bold;
    text-align: left;
    font-family: "Metal Mania";
    color:#777;
    font-size: 20px;
  }
  .right a.active{
    color:red
  }
  
  .bgtext {
    height: 30px;
    color: white;
    font-weight: bold;
    font-size: 20px;
    font-family: "Metal Mania";
    
  }
  .header {
    width: 100%;
    height: 5rem;
    background-color:rgb(104, 154, 154);
    box-shadow: 0 1px 4px rgb(104, 154, 154);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    font-family: 'metal-mania';
  
   
  }
  
  .logo {
    font-family:'metal mania';
    font-size: 3rem;
    color: white;
    margin: 0;
  }
  
  .header ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: baseline;
    font-family: 'Metal-Mania';
  }
  
  .header li {
    margin: 0 1rem;
  }
  
  .header a {
    text-decoration: none;
    color: white;
    font-weight: bold;
  }
  
  .header button {
    font: inherit;
    background-color: rgb(85, 83, 83);
    border: 1px solid rgb(85, 83, 83);
    color: white;
    font-weight: bold;
    padding: 0.5rem 1.5rem;
    border-radius: 6px;
    cursor: pointer;
  }
  
  .header a:hover {
    color: white;
    text-decoration: underline;
  }
  
  .header button:hover {
    background-color:   rgb(57, 53, 53);
    color:white;
  }
  .icon {
    width:0.35rem;
    height: 0.35rem;
    margin-right: 0.5rem;
  }
  .number {
    background-color:darkgray;
    padding: 0.15rem;
    border-radius: 25px;
    margin-left: 1rem;
      
  }